export const StyledConnectLinkMobile = styled.a`
padding: 10px;
border-radius: 50px;
border: none;
background-color: var(--secondary);
font-weight: bold;
color: var(--accent);
font-size: 30px;
width: 210px;
cursor: pointer;
box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
-webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
-moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
:active {
  box-shadow: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
}
  text-decoration: none;
`;

function App() {
    let Price;
    let now = Math.floor(Date.now() / 1000);
    const dispatch = useDispatch();
    const blockchain = useSelector((state) => state.blockchain);
    const data = useSelector((state) => state.data);
    const [CONFIG, SET_CONFIG] = useState({
        CONTRACT_ADDRESS: "",
        SCAN_LINK: "",
        NETWORK: {
            NAME: "",
            SYMBOL: "",
            ID: 0,
        },
        NFT_NAME: "",
        SYMBOL: "",
        PUBLIC: 0,
        WHITELIST: 0,
        FREE: 0,
        SHOW_BACKGROUND: false,
    });

    if (now >= CONFIG.WHITELIST && now <= CONFIG.PUBLIC) {
        Price = 0.00911;
        console.log(Price);
    } else if (now >= CONFIG.PUBLIC && now <= CONFIG.FREE) {
        Price = 0.03;
        console.log(Price);
    }else {
        Price = 0;
        console.log(Price);
    }

    
    const [claimingNft, setClaimingNft] = useState(false);
    const [feedback, setFeedback] = useState(``);
    const [mintAmount, setMintAmount] = useState(1);
    const [totalCost, setTotalCost] = useState(Price);
    const [Whitelisted, setWhitelisted] = useState([]);
    const claimNFTs = async () => {

        const leaf = Whitelisted.map(addr => keccak256(addr));
        const Merkletree = new MerkleTree(leaf, keccak256, { sortPairs: true });

        const rootHash = Merkletree.getRoot().toString('hex');
        const adddd = keccak256(blockchain.account);

        const proof = Merkletree.getHexProof(adddd);
        setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);

        console.log("Root Hash : 0x" + rootHash);

        let publicCost = await blockchain.smartContract.publicCost();
        let whitelistCost = await blockchain.smartContract.whitelistCost();
        let totalCostWei;
        setClaimingNft(true);

        if (now >= CONFIG.WHITELIST && now <= CONFIG.PUBLIC) {
            totalCostWei = String(whitelistCost * mintAmount);
            try {
                let TX = await blockchain.smartContract
                    .whitelistMint(mintAmount, proof, {value: totalCostWei})
                console.log(TX)
                let Res = await TX.wait(1)
                console.log(Res)
                setFeedback(
                    `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
                );
                setClaimingNft(false);
            } catch (error) {
                if (error.code === -32603) {
                    setFeedback(error.data.message.slice(20));
                } else if (error.code === -32000) {
                    setFeedback("Insufficient Funds.");
                } else if (error.code === 4001) {
                    setFeedback("Mint Canceled , Please Try Again.");
                } else {
                    setFeedback("Something Went Wrong , Please Try Again.");
                }
                setClaimingNft(false);
            }

        } else if (now >= CONFIG.PUBLIC && now <= CONFIG.FREE) {
            totalCostWei = String(publicCost * mintAmount);
            try {
                let TX = await blockchain.smartContract
                    .mint(mintAmount, {value: totalCostWei});
                console.log(TX)
                let Res = await TX.wait(1)
                console.log(Res)
                setFeedback(
                    `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
                );
                setClaimingNft(false);
            } catch (error) {
                if (error.code === -32603) {
                    setFeedback(error.data.message.slice(20));
                } else if (error.code === -32000) {
                    setFeedback("Insufficient Funds.");
                } else if (error.code === 4001) {
                    setFeedback("Mint Canceled , Please Try Again.");
                } else {
                    setFeedback("Something Went Wrong , Please Try Again.");
                }
                setClaimingNft(false);
            }

        }else if (now >= CONFIG.FREE){
            try {
                let TX = await blockchain.smartContract
                    .freeMint(mintAmount)
                console.log(TX)
                let Res = await TX.wait(1)
                console.log(Res)
                setFeedback(
                    `WOW, the ${CONFIG.NFT_NAME} is yours! go visit Opensea.io to view it.`
                );
                setClaimingNft(false);
            } catch (error) {
                if (error.code === -32603) {
                    setFeedback(error.data.message.slice(20));
                } else if (error.code === -32000) {
                    setFeedback("Insufficient Funds.");
                } else if (error.code === 4001) {
                    setFeedback("Mint Canceled , Please Try Again.");
                } else {
                    setFeedback("Something Went Wrong , Please Try Again.");
                }
                setClaimingNft(false);
            }
        }else{
            setFeedback("Mint Failed");
            setClaimingNft(false);
        }

        setClaimingNft(false);

    };


    const decrementMintAmount = () => {
        let newMintAmount = mintAmount - 1;
        if (newMintAmount < 1) {
            newMintAmount = 1;
        }
        setMintAmount(newMintAmount);
        let newAmount = Number((newMintAmount * Price).toFixed(5));
        setTotalCost(newAmount)

    };

    const incrementMintAmount = () => {
        let newMintAmount = mintAmount + 1;
        if (newMintAmount > 10) {
            newMintAmount = 10;
        }
        setMintAmount(newMintAmount);
        console.log(newMintAmount);
        let newAmount = Number((newMintAmount * Price).toFixed(5));
        console.log(Price);
        setTotalCost(newAmount)

    };

    const getData = () => {
        if (blockchain.account !== "" && blockchain.smartContract !== null) {
            dispatch(fetchData(blockchain.account));
        }
    };



    const getWhitelistedAddresses = async () => {
        const Data = await fetch("/config/Addresses.txt", {
            headers: {
                "Content-Type": "plain/text",
                Accept: "plain/text",
            },
        });
        var Addresses = await Data.text();
        Addresses = Addresses.split('\n')
        setWhitelisted(Addresses)
    }

    async function isConnected() {
        const accounts = await window.ethereum.request({method: 'eth_accounts'});       
        if (accounts.length) {
           dispatch(connect());
        } else {
          //  console.log("Metamask is not connected");
        }
      }
    async function maxCost() {
        let provider = blockchain.ethers.getDefaultProvider()
        let balance = await provider.getBalance(blockchain.account);
        let cost = blockchain.ethers.utils.parseUnits(String(Price), "ether");
        let amount = balance / cost;
        if (amount == 0){
            alert("Please Fund Your Account");
        }else{
            if (amount > 10){
            setMintAmount(10);
            }else{
            setMintAmount(amount);
            }
        }
      }

      const getConfig = async () => {
        const configResponse = await fetch("/config/config.json", {
            headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
            },
        });
        const config = await configResponse.json();
        if (now >= config.WHITELIST && now <= config.PUBLIC) {
            Price = 0.00911;
            setTotalCost(Price);
            console.log(Price);
        } else if (now >= config.PUBLIC && now <= config.FREE) {
            Price = 0.03;
            setTotalCost(Price);
            console.log(Price);
        }else {
            Price = 0;
            setTotalCost(Price);
            console.log(Price);
        }
        SET_CONFIG(config);
    };

    useEffect(() => {
        isConnected();
        getConfig();
        getWhitelistedAddresses();
        // updateData();
    }, []);

    useEffect(() => {
        getData();
    }, [blockchain.account]);

    return (
        <s.Screen style={{ padding: '24px' }}>
            <s.Container
                flex={1}
                ai={"center"}
                style={{ padding: 24, backgroundColor: "var(--primary)" , alignItems: 'flex-end' }}
                image="/config/images/bg.png"
            >
        {isMobile && !window.ethereum ? (
        <ConnectButton
        >
           <StyledConnectLinkMobile href="https://metamask.app.link/dapp/mint.duckzillas.com"> CONNECT </StyledConnectLinkMobile>
        </ConnectButton>
        ):(
        blockchain.account === "" ||
                                    blockchain.smartContract === null ? (
                                        
                <ConnectButton
                    onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                    }}
                >
                    CONNECT
                </ConnectButton>
                                    ):
                <ConnectButton
                    disabled={1}
                >
                    CONNECTED
                </ConnectButton>
                
                ) }
                
                <s.SpacerSmall />
                <ResponsiveWrapper flex={1} style={{ }} test>

                    <s.Container
                        flex={2}
                        jc={"center"}
                        ai={"center"}
                        style={{
                            backgroundColor: "var(--accent)",
                            padding: 24,
                            borderRadius: 50,
                            maxWidth: "543px",
                            // border: "4px dashed var(--secondary)",
                            boxShadow: "0px 5px 11px 2px rgba(0,0,0,0.7)",
                        }}
                    >
                        <s.TextTitle
                            style={{
                                textAlign: "center",
                                fontWeight: "500",
                                color: "var(--accent-text)",
                            }}
                        >
                           MINT DUCKZILLAS
                        </s.TextTitle>
                        <s.TextDescription
                            style={{
                                textAlign: "center",
                                color: "var(--secondary-text)",
                            }}
                        >
                             QUACKLIST 8,888 NFTS
                        </s.TextDescription>
                        
                        <s.SpacerSmall />
                        {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
                            <>
                                <s.TextTitle
                                    style={{ textAlign: "center", color: "var(--accent-text)" }}
                                >
                                    The sale has ended.
                                </s.TextTitle>
                                <s.TextDescription
                                    style={{ textAlign: "center", color: "var(--accent-text)" }}
                                >
                                    You can still find {CONFIG.NFT_NAME} on
                                </s.TextDescription>
                                <s.SpacerSmall />
                                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                                    {CONFIG.MARKETPLACE}
                                </StyledLink>
                            </>
                        ) : (
                            <>
                                
                                    <>
                                        {blockchain.errorMsg !== "" ? (
                                            <>
                                                <s.SpacerSmall />
                                                <s.TextDescription
                                                    style={{
                                                        fontSize: '25px',
                                                        textAlign: "center",
                                                        color: "var(--accent-text)",
                                                        fontFamily: 'sans-serif'
                                                    }}
                                                >
                                                    {blockchain.errorMsg}
                                                </s.TextDescription>
                                            </>
                                        ) : null}
                                        <s.TextDescription
                                            style={{
                                                fontSize: '25px',
                                                textAlign: "center",
                                                color: "var(--accent-text)",
                                                fontFamily: 'sans-serif'

                                            }}
                                        >
                                            {feedback}
                                        </s.TextDescription>
                                        <s.SpacerMedium />
                                        <s.SpacerLarge />
                                        <s.SpacerLarge />

                                        <s.Container style={{ 
                                            justifyContent: 'space-between',
                                            borderBottom: '5px solid var(--accent-text)'
                                         }} ai={"center"} jc={"center"} fd={"row"}>
                                            <>
                                                <s.TextDescription
                                                    style={{
                                                        textAlign: "center",
                                                        color: "var(--accent-text)",
                                                    }}
                                                >
                                                    AMOUNT
                                                </s.TextDescription> 

                                                    
                                                <StyledRoundButton
                                                    style={{ lineHeight: 0.4, alignItems : 'baseline' }}
                                                    disabled={claimingNft ? 1 : 0}
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        decrementMintAmount();
                                                    }}
                                                >
                                                    -
                                                </StyledRoundButton>

                                                <s.TextDescription
                                                    style={{
                                                        textAlign: "center",
                                                        color: "var(--accent-text)",
                                                    }}
                                                >
                                                    {mintAmount}
                                                </s.TextDescription>

                                                <StyledRoundButton
                                                    disabled={claimingNft ? 1 : 0}
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        incrementMintAmount();
                                                    }}
                                                >
                                                    +
                                                </StyledRoundButton>
                                            </>

                                            {blockchain.account === "" ||
                                    blockchain.smartContract === null ? (
                                        <StyledMaxButton
                                                style={{ lineHeight: 0.4 }}
                                                onClick={(e) => {
                                                    e.preventDefault();
                                                    alert("Please Connect Your Metamask Wallet")
                                                }}
                                            >
                                                MAX
                                            </StyledMaxButton>
                                    ):
                                            <StyledMaxButton
                                                style={{ lineHeight: 0.4 }}
                                                disabled={claimingNft ? 1 : 0}
                                                onClick={(e) => {
                                                    e.preventDefault();
                                                    maxCost();
                                                }}
                                            >
                                                MAX
                                            </StyledMaxButton>
}
                                        </s.Container>
                                        <s.SpacerSmall />
                                        <s.Container style={{ 
                                            justifyContent: 'space-between',
                                            borderBottom: '5px solid var(--accent-text)'
                                         }} ai={"center"} jc={"center"} fd={"row"}>
                                                <s.TextDescription
                                                    style={{
                                                        textAlign: "center",
                                                        color: "var(--accent-text)",
                                                    }}
                                                >
                                                    TOTAL
                                                </s.TextDescription>           
                                                <s.TextDescription
                                                    style={{
                                                        textAlign: "center",
                                                        color: "var(--accent-text)",
                                                    }}
                                                >
                                                    {totalCost} ETH
                                                </s.TextDescription> 
                                        </s.Container>
                                        <s.SpacerSmall />
                                        <s.SpacerSmall />
                                        <s.Container ai={"center"} jc={"center"} fd={"row"}>
                                        {blockchain.account === "" ||
                                    blockchain.smartContract === null ? (
                                            <StyledButton
                                                disabled={0}
                                                onClick={(e) => {
                                                    alert("Please Connect Your Metamask Wallet")
                                                }}
                                            >
                                                {claimingNft ? "BUSY" : "MINT"}
                                            </StyledButton>
                                    ):<StyledButton
                                    disabled={claimingNft ? 1 : 0}
                                    onClick={(e) => {
                                        e.preventDefault();
                                        claimNFTs();
                                        getData();
                                    }}
                                >
                                    {claimingNft ? "BUSY" : "MINT"}
                                </StyledButton>}
                                            
                                        </s.Container>
                                        <s.SpacerMedium />

                                        <s.TextDescription
                                                    style={{
                                                        textAlign: "center",
                                                        fontSize: 30,
                                                        color: "#bbbbbb",
                                                    }}
                                                >
                                                    MAKE SURE YOUR METAMASK IS CONNECTED
                                        </s.TextDescription> 
                                    </>
                                {/* )} */}
                            </>
                        )}
                        <s.SpacerMedium />
                        
                    </s.Container>

                    
                </ResponsiveWrapper>
                <s.SpacerMedium />
                
               
           
            </s.Container>
        </s.Screen>
    );
}

export default App;
